<template>
    <div v-if="seccion != null && seccion != 'undefined'">
        <Header></Header>
        <v-container style="margin-top:50px; border: solid 1px lightgray;
                                        border-radius: 15px;">
            <v-row>
                <v-col cols="12">
                    <v-text-field v-model="seccion.titulo" placeholder="Titulo de la Sección"></v-text-field>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="12">
                    <v-text-field v-model="seccion.subtitulo" placeholder="Subtitulo de la Sección"></v-text-field>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="12" style="text-align:right;">
                    <v-btn class="btn btn-primary" @click="salir()">Salir</v-btn>&nbsp;
                    <v-btn class="btn btn-primary" @click="updateSeccion()">Guardar</v-btn>
                </v-col>
            </v-row>
        </v-container>
        <v-container style="margin-top:50px; border: solid 1px lightgray;
                                        border-radius: 15px; margin-bottom: 50px;">
            <v-row>
                <v-col cols="6">
                    <h3>Contenido Sección</h3>
                </v-col>
                <v-col cols="6" style="text-align:right;">
                    <v-btn class="btn btn-primary" @click="agregarContenido()">Agregar Contenido</v-btn>
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="12">
                    <hr />
                </v-col>
            </v-row>
            <v-row>
                <v-col cols="12">
                    <table style="width: 100%">
                        <Draggable :list="seccion.lstContenido" tag="tbody" @end="saveorden()">
                            <tr v-for="(item, index) in seccion.lstContenido" :key="index" :style="[
                                item.activo
                                    ? { padding: '5px' }
                                    : {
                                        'background-color': '#d3d3d359',
                                        opacity: '0.5',
                                    },
                            ]">
                                <td>{{ item.titulo_contenido }}</td>
                                <td>
                                    <v-menu top :close-on-content-click="closeOnContentClick">
                                        <template v-slot:activator="{ on, attrs }">
                                            <v-btn text v-bind="attrs" v-on="on" style="float: right">
                                                <v-icon class="fa fa-bars"></v-icon>
                                            </v-btn>
                                        </template>
                                        <v-list>
                                            <v-list-item>
                                                <v-btn text @click="editarContenido(item.id, item.titulo_contenido, item.contenido_contenido)
                                                ">
                                                    <v-icon small class="fa fa-edit"></v-icon>&nbsp; Editar
                                                </v-btn>
                                            </v-list-item>
                                            <v-list-item>
                                                <SubirArchivos :id_contenido="item.id"></SubirArchivos>
                                            </v-list-item>
                                            <v-list-item>
                                                <v-btn text v-if="item.activo"
                                                    @click="activa_desactiva(item.id, false)">
                                                    <v-icon left class="fa fa-eye"></v-icon>
                                                    Desactivar
                                                </v-btn>
                                                <v-btn text v-else @click="activa_desactiva(item.id, true)">
                                                    <v-icon left class="fa fa-eye-slash"></v-icon>
                                                    Activar
                                                </v-btn>
                                            </v-list-item>
                                            <v-list-item>
                                                <v-btn text @click="logicDelete(item.id)">
                                                    <v-icon small class="fa fa-remove"></v-icon>&nbsp; Eliminar
                                                </v-btn>
                                            </v-list-item>
                                        </v-list>
                                    </v-menu>
                                </td>
                            </tr>
                        </Draggable>
                    </table>
                </v-col>
            </v-row>
        </v-container>
        <Footer></Footer>
        <v-dialog transition="dialog-top-transition" max-width="600" v-model="dialogOk">
            <template v-slot:default="dialogOk">
                <v-card>
                    <v-toolbar style="background-color: green !important;
              border-color: green !important;
            " dark>Felicidades!</v-toolbar>
                    <v-card-text>
                        <div style="
                margin-top: 20px;
                text-align: center;
                color: black;
                font-size: 20px;
                font-family: 'DM Sans';
              ">
                            Los datos se actualizaron de forma correcta
                        </div>
                    </v-card-text>
                    <v-card-actions class="justify-end">
                        <v-btn text @click="dialogOk.value = false">Salir</v-btn>
                    </v-card-actions>
                </v-card>
            </template>
        </v-dialog>
        <v-dialog transition="dialog-top-transition" max-width="600" v-model="dialogContenido">
            <template v-slot:default="dialogContenido">
                <v-card>
                    <v-toolbar style="
              background-color: green !important;
              border-color: green !important;
            " dark>Contenido</v-toolbar>
                    <v-card-text>
                        <v-text-field v-model="titulo_contenido" style="margin-top:40px;"
                            placeholder="Titulo de la Pestaña / Panel de Extencion"></v-text-field>
                        <h4 style="font-size: 20px;  margin-bottom: 10px;">Contenido de la sección</h4>
                        <VueEditor v-model="contenido_contenido"></VueEditor>
                    </v-card-text>
                    <v-card-actions class="justify-end">
                        <v-btn text @click="dialogContenido.value = false">Cancelar</v-btn>
                        <v-btn text @click="guardarContenido()">Guardar</v-btn>
                    </v-card-actions>
                </v-card>
            </template>
        </v-dialog>
        <v-dialog transition="dialog-top-transition" max-width="600" v-model="dialogArchivos">
            <template v-slot:default="dialogArchivos">
                <v-card>
                    <v-toolbar style="
              background-color: green !important;
              border-color: green !important;
            " dark>Contenido</v-toolbar>
                    <v-card-text>
                        <v-text-field v-model="titulo_contenido" style="margin-top:40px;"
                            placeholder="Titulo de la Pestaña / Panel de Extencion"></v-text-field>
                        <h4 style="font-size: 20px;  margin-bottom: 10px;">Contenido de la sección</h4>
                        
                    </v-card-text>
                    <v-card-actions class="justify-end">
                        <v-btn text @click="dialogArchivos.value = false">Cancelar</v-btn>
                        <v-btn text @click="guardarContenido()">Guardar</v-btn>
                    </v-card-actions>
                </v-card>
            </template>
        </v-dialog>
        <v-dialog transition="dialog-top-transition" max-width="600" v-model="dialogDelete">
            <template v-slot:default="dialogDelete">
                <v-card>
                    <v-toolbar style="
              background-color: red !important;
              border-color: red !important;
            " dark>
                        <v-card-title>Confirmar</v-card-title>
                    </v-toolbar>
                    <v-card-text>
                        <div style="
                margin-top: 20px;
                text-align: center;
                color: black;
                font-size: 20px;
                font-family: 'DM Sans';
              ">
                            ¿Esta seguro de eliminar el contenido?
                        </div>
                    </v-card-text>
                    <v-card-actions class="justify-end">
                        <v-btn text @click="dialogDelete.value = false">Cancelar</v-btn>
                        <v-btn text @click="confirmaDeleteSeccion()">Eliminar</v-btn>
                    </v-card-actions>
                </v-card>
            </template>
        </v-dialog>
    </div>
</template>
<style scoped>

</style>
<script>
import Draggable from "vuedraggable";
import Header from "../../components/Headers/Tramites_Paso.vue";
import Footer from "../../components/Footer.vue";
import SubirArchivos from "../../components/SubirArchivos.vue";
import { VueEditor } from 'vue2-editor'
export default {
    data: () => {
        return {
            dialogArchivos: false,
            dialogDelete: false,
            dialogOk: false,
            dialogContenido: false,
            closeOnContentClick: false,
            seccion: null,
            id_contenido: 0,
            titulo_contenido: "",
            contenido_contenido: ""
        };
    },
    components: {
        Header,
        Footer,
        Draggable,
        VueEditor,
        SubirArchivos,
    },
    async mounted() {
        try {
            if (this.$storage.getTextOrInt("codUsuario") == null) {
                this.$router.push("/");
            }
            this.seccion = (await this.$http.get("/Seccion/getByPk?pk=" + this.$route.params.id)).data;
        } catch (error) { }
    },
    methods: {
        async activa_desactiva(id, estado) {
            let post = {
                id: id,
                activo: estado,
                id_seccion: this.$route.params.id,
                _tipo: "",
            };
            this.seccion = (await this.$http.post("/Seccion/activaDesactivaContenido", post)).data;
            this.closeOnContentClick = false;
        },
        salir() {
            this.$router.push("/PageConfig/" + this.seccion.id_page);
        },
        async updateSeccion() {
            let post = {
                id: this.$route.params.id,
                titulo: this.seccion.titulo,
                subtitulo: this.seccion.subtitulo,
                _tipo: "",
            };
            this.seccion = (await this.$http.post("/Seccion/update", post)).data;
            this.dialogOk = true;
        },
        agregarContenido() {
            this.id_contenido = 0;
            this.titulo_contenido = "";
            this.contenido_contenido = "";
            this.dialogContenido = true;
        },
        editarContenido(id, titulo_contenido, contenido_contenido) {
            this.id_contenido = id;
            this.titulo_contenido = titulo_contenido;
            this.contenido_contenido = contenido_contenido;
            this.dialogContenido = true;
        },
        async guardarContenido() {
            alert(this.contenido_contenido);
            let post = {
                id: this.id_contenido,
                titulo_contenido: this.titulo_contenido,
                contenido_contenido: this.contenido_contenido,
                id_seccion: this.$route.params.id,
            };
            if (this.id_contenido == 0) {
                this.seccion = (await this.$http.post("/Seccion/insertContenido", post)).data;
            }
            else {
                this.seccion = (await this.$http.post("/Seccion/updateContenido", post)).data;
            }
            this.dialogOk = true;
            this.dialogContenido = false;
        },
        async saveorden() {
            (await this.$http.post("/Seccion/reordenarContenido", this.seccion));
        },
        logicDelete(id) {
            this.id = id;
            this.dialogDelete = true;
        },
        async confirmaDeleteSeccion() {
            try {
                let post = {
                    id: this.id,
                    id_seccion: this.$route.params.id,
                };
                this.seccion = (await this.$http.post("/Seccion/deleteContenido", post)).data;
                this.dialogDelete = false;
            } catch (error) { }
        },
    },
};
</script>